import {AWidget} from "./abstract/a-widget";
import {EBasicColorStatus} from "../component/enum/e-basic-color-status";
import jquery = require("jquery");
/**
 * Uma mensagem de alerta básica.
 * 
 * <img src="../../doc_assets/alert_msg_exemple1.png">
 * 
 * @example
 * let mymsg:Alert = new Alert("Aviso Teste!");
 *   
 * @export
 * @class Alert
 * @extends {AWidget}
 * @memberof module:mvcomponents/widget
 */
export class Alert extends AWidget {
	constructor(text: string = "") {
		super("div", `<div class="inputtt col-xs-12 col-md-12 form-group alert alert-success fade in" data-alert="alert">${text}</div>`);
		jquery(this.element).addClass("Alert col-xs-12 col-md-12");
	}
	
	/**
	 * Informa qual o texto que será exibido 
	 * 
	 * @param {string} text
	 * @return {this}
	 */
	public setText(text: string): this {
		jquery(this.element).find(".inputtt").text(text);
		return this;
	}

	/**
	 * Obtém o texto 
	 * 
	 * @return {string}
	 */
	public getText(): string {
		return jquery(this.element).find(".inputtt").text();
	}

	/**
	 * Informa qual a cor que o alerta será exibido 
	 * 
	 * @param {EBasicColorStatus} basic_color
	 * @return {this}
	 */
	public setColor(basic_color: EBasicColorStatus): this {
		var classes = jquery(this.element).find(".inputtt").attr("class");
		var ns = classes.replace(/alert\-([^\s]*)/, `alert-${EBasicColorStatus[basic_color].toLowerCase()}`);
		jquery(this.element).find(".inputtt").removeClass().addClass(ns);
		return this;
	}

	private insertText(text: string, type_append: string): void {
		jquery(this.element).find(".inputtt:first")[type_append](text);
	}

	/**
	 * Insere um texto no inicio 
	 * 
	 * @param {string} text
	 * @return {this}
	 */
	public prependText(text: string): this {
		this.insertText(text, "prepend");
		return this;
	}
	
	/**
	 * Insere um texto no fim 
	 * 
	 * @param {string} text
	 * @return {this}
	 */
	public appendText(text: string): this {
		this.insertText(text, "append");
		return this;
	}
}
